import React, { Component, Fragment } from 'react';
import Modal from '../Modal/Modal';
import css from './Card.module.css';

export default class Card extends Component {
  state = { isModalOpen: false };

  clickHandler = e => {
    this.setState({ isModalOpen: true });
  };

  cancelHandler = e => {
    if (e.target === e.currentTarget) {
      this.setState({ isModalOpen: false });
    }
    return;
  };

  render() {
    const { isModalOpen } = this.state;
    const { img } = this.props;
    return (
      <Fragment>
        {isModalOpen && <Modal onCancel={this.cancelHandler} />}
        <div className={css.wrapper} onClick={this.clickHandler}>
          <img src={img} className={css.img} />
          <h2 className={css.cardTitle}>Название работы</h2>
          <span className={css.cardTrigger}>Подробнее</span>
        </div>
      </Fragment>
    );
  }
}
